<template>
<div id="calculator">
  <span class="input">
    <h3>{{ calcmode }}</h3>
    <input
      class="calcinput"
      disabled
      :value="display"
      :class="calcmode"
    >
  </span>
  <div class="info">
    <p>
      How to use this calculator </br>
      Hotkeys </br>
      C: Clear </br>
      x: Represent hex number </br>
      <: Right Shift </br>
      >: Left Shift </br>
      %: Modulus </br>
      q: Or </br>
      w: And </br>
      e: Not </br>
      r: Xor </br>
    </p>
  </div>
</div>
</template>

<script>
let keys;
export default {
  name: 'calculator',
  data() {
    return {
      display: '0',
      calcmode: 'DEC',
      lastaction: '',
    };
  },
  methods: {
    input(e) {
      if (e.key in keys) {
        keys[e.key]();
      } else {
        console.log(e);
      }
    },
    back() {
      switch (this.lastaction) {
        case 'mode':
          this.calcmode = 'DEC';
          break;
        default:
      }
    },
    clear() {
      console.log('cleared');
    },
    calculate() {
    },
    setMode(mode) {
      return () => {
        this.calcmode = mode;
        this.lastaction = 'mode';
      };
    },
    op(op) {
      console.log(op);
    },
  },
  mounted() {
    document.addEventListener('keyup', this.input);
    keys = {
      x: this.setMode('HEX'),
      o: this.setMode('OCT'),
      b: this.setMode('BIN'),
      c: this.clear,
      Backspace: this.back,
    };
  },
};
</script>

<style>
div#calculator {
  display: flex;
  flex-direction: column;
}
div.info {
  text-align: left;
  margin: 0 1em;
}
span.input {
  display: flex;
  background-color: #ccc;
}
span.input > h3 {
  display: inline-flex;
  align-items: center;
  height: 100%;
  margin: auto 0;
}
input.calcinput,
input.calcinput:disabled {
  background-color: #ccc;
  font-size: 8em;
  text-align: right;
  width: 10em;
  height: 2em;
  color: #000;
  border: none;
  cursor: default;
}
input.calcinput:focus {
  outline: none;
  color: transparent;
  text-shadow: 0 0 0 black;
}
</style>



